import React from 'react';
import { ListGroup, Row, Col } from 'reactstrap';
import {
  faCalendar,
  faEnvelope,
  faMapMarker,
  faGlobeAsia,
  faInfoCircle,
} from '@fortawesome/free-solid-svg-icons';
// Own Component
import DashboardCard from 'src/components/DashboardCard';
import Information from 'src/components/Information';


const BasicInformation = (props) => {

  const data = [
    {
      icon: faCalendar,
      title: 'Job title:',
      description: props.user.jobTitle,
    },
    {
      icon: faMapMarker,
      title: 'Location:',
      description: props.user.location,
    },
  ];
  const data2 = [
    {
      icon: faEnvelope,
      title: 'Email:',
      description: props.user.email,
    },
  ];
  return (
    <DashboardCard modalName="basic" title="Basic information" action className="mb-5">
      <Row>
        <Col lg={6}>
          <ListGroup>
            {data && data.map((item, i) => <Information key={i} {...item} />)}
          </ListGroup>
        </Col>
        <Col lg={6}>
          <ListGroup>
            {data2 && data2.map((item, i) => <Information key={i} {...item} />)}
          </ListGroup>
        </Col>
      </Row>
    </DashboardCard>
  );
};

export default BasicInformation;
